body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.clearing {
	clear: both;
}

.loading {
  position: relative;
  top: 30vh;
  z-index: 10; }
  .loading .inner-tube {
    margin-left: 50%;
    position: absolute; }
  .loading .spinner-border {
    margin-left: -50%; }

.is-public button {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 32px 32px;
  width: 40px; }
  .is-public button.private {
    background-image: url(/static/media/private.d7365d01.svg); }
  .is-public button.public {
    background-image: url(/static/media/public.20e4a36e.svg); }

.list-summary {
  border: 1px solid #999;
  border-radius: 8px;
  margin: 0 8px 8px 0;
  overflow: hidden;
  padding: 8px; }
  .list-summary a {
    color: #333; }
    .list-summary a:hover {
      text-decoration: none; }
  .list-summary .list-name {
    float: left;
    font-weight: bold; }
  .list-summary .list-summary-controls {
    float: right; }
    .list-summary .list-summary-controls .is-public {
      float: left; }
    .list-summary .list-summary-controls .btn-danger {
      float: left;
      margin-left: 8px; }

.list-description {
  background: #fff;
  clear: both;
  display: block;
  line-height: 1.5em;
  max-height: 3em;
  overflow: hidden;
  position: relative; }
  .list-description::before {
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #fff 80%);
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fff 80%);
    content: "\200C";
    display: block;
    position: absolute;
    right: 0;
    text-align: center;
    top: 1.5em;
    width: 4em; }

.list-created-by {
  float: left;
  margin: 6px 0; }

.lists-list .tabs {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 6px 0; }
  .lists-list .tabs ul {
    border: 1px solid #424a6c;
    border-radius: 12px;
    float: left;
    list-style-type: none;
    margin: 0;
    overflow: hidden;
    padding: 0; }
  .lists-list .tabs span {
    cursor: pointer;
    display: inline-block;
    padding: 6px 12px; }
    .lists-list .tabs span.selected {
      background-color: #424a6c;
      color: #fff; }

.lists-list .create-list {
  margin: 8px 0; }

.lists-list .top-level-lists-control .form-check-input {
  margin: 8px 0;
  position: relative; }

.lists-list .top-level-lists-control label {
  font-size: 1rem; }

.pagination {
  -webkit-justify-content: center;
          justify-content: center; }
  .pagination button {
    color: #007bff;
    padding-right: 0.2rem; }
    .pagination button:hover {
      text-decoration: underline; }
  .pagination .active button {
    color: #333; }
  .pagination .spacer {
    width: 1em; }

.btn-primary {
  margin-left: 8px; }

.editable-text-field input {
  padding: 6px 12px; }

.editable-text-field .placeholder {
  color: #aaa; }

.editable-text-field .btn-primary {
  margin-left: 8px; }

.editable-text-field .text {
  display: inline-block;
  margin: 6px 0;
  max-height: 3em;
  overflow: hidden;
  position: relative; }
  .editable-text-field .text.expanded {
    max-height: none; }
    .editable-text-field .text.expanded .fader {
      display: none; }
  .editable-text-field .text p {
    margin: 0;
    padding: 0; }
  .editable-text-field .text .fader {
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #fff 80%);
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fff 80%);
    display: block;
    height: 1.5em;
    position: absolute;
    right: 0;
    text-align: center;
    top: 1.5em;
    width: 4em; }

.editable-text-field button.show-more {
  float: right; }

.editable-text-field .controls {
  margin-bottom: 6px; }

.editable-text-field .hint {
  color: #666;
  font-size: 1rem !important;
  font-weight: normal !important;
  margin-left: 6px; }
  .editable-text-field .hint a {
    color: #666;
    text-decoration: underline; }

.item-header {
  font-weight: bold; }
  .item-header .editable-text-field {
    float: left;
    margin-left: 0; }
    .item-header .editable-text-field.editing {
      float: none; }
    .item-header .editable-text-field .placeholder {
      display: inline-block;
      padding: 6px 0; }

.item-body {
  clear: both; }

.item-container {
  border: 1px solid #999;
  border-radius: 8px;
  margin: 0 8px 8px 0;
  overflow: hidden;
  padding: 8px; }
  .item-container label {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    width: 1px; }
  .item-container .order {
    float: left;
    margin-right: 8px;
    padding: 6px 0; }
  .item-container button.create-childlist {
    float: right; }
  .item-container button.move-up {
    margin-right: 6px; }
  .item-container .child-list {
    float: right;
    padding: 6px 0; }

.organizer {
  float: left;
  margin: 0 8px 8px 0; }
  .organizer.open {
    clear: both; }
  .organizer .lists {
    background: #eee;
    border: 1px solid #666;
    border-radius: 8px;
    margin-top: 8px;
    padding: 8px; }
    .organizer .lists .inner-tube {
      max-height: 500px;
      overflow: auto; }
  .organizer .organize {
    padding: 1px 6px; }

.organizer-list.row {
  margin: 4px 0; }

.organizer-list .show-items {
  float: left;
  width: 36px; }

.organizer-list .name {
  display: inline-block;
  font-weight: bold;
  margin-left: 12px;
  padding: 6px 0; }

.organizer-list .items {
  margin-left: 56px; }

.organizer-list .item {
  display: inline-block;
  width: 100%; }
  .organizer-list .item > span {
    background: #eee;
    border: 1px solid transparent;
    color: #212529;
    float: left;
    padding: 6px 12px 6px 8px; }
    .organizer-list .item > span .order {
      display: inline-block;
      padding: 0 6px 0 0;
      text-align: right;
      width: 2.2rem; }
  .organizer-list .item .child-list {
    color: #666; }
  .organizer-list .item.selected > span {
    background: #fff;
    border: 1px solid #212529;
    border-radius: 1rem; }
  .organizer-list .item.selected .child-list {
    background: transparent;
    border: 1px solid transparent; }

.list-detail-controls {
  float: right; }
  .list-detail-controls .is-public {
    float: left; }
  .list-detail-controls .btn-danger {
    float: left;
    margin-left: 8px; }

.list-detail {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }
  .list-detail > div {
    width: 100%; }
    @media (min-width: 992px) {
      .list-detail > div {
        max-width: 720px; } }
  .list-detail .breadcrumbs {
    float: left; }
  .list-detail .hint {
    color: #666; }
  .list-detail .list-organizer {
    float: left;
    margin-left: 8px; }
  .list-detail .list-name label {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    width: 1px; }
  .list-detail .list-name .editable-text-field {
    float: left; }
  .list-detail .list-name span {
    font-size: 1.5rem;
    font-weight: bold; }
    .list-detail .list-name span.hint {
      font-size: 1rem;
      font-weight: normal; }
  .list-detail .list-description {
    max-height: none; }
    .list-detail .list-description label {
      border: 0;
      clear: both;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      padding: 0;
      position: absolute;
      width: 1px; }
  .list-detail .privacy-warning {
    color: #666; }

.valid-feedback {
  color: #28a745;
  display: block;
  font-size: 80%;
  margin-top: .25rem;
  width: 100%; }
  .valid-feedback::before {
    content: "\2714   "; }

.invalid-feedback {
  color: #dc3545;
  font-size: 80%;
  margin-top: .25rem;
  width: 100%; }
  .invalid-feedback::before {
    content: "\2718   "; }

.flash-error {
  border-radius: 8px;
  padding: 8px; }
  .flash-error.error {
    border: 1px solid #dc3545;
    color: #dc3545; }
    .flash-error.error::before {
      content: "\2718   "; }
  .flash-error.success {
    border: 1px solid #28a745;
    color: #28a745; }
    .flash-error.success::before {
      content: "\2714   "; }

.btn-default {
  border-color: #ccc; }

